@import url(https://fonts.googleapis.com/css?family=Roboto:300);

header .header{
  background-color: #fff;
  height: 45px;
}
header a img{
  width: 134px;
  margin-top: 4px;
}
.login-page {
  width: 360px;
  padding: 40% 0 0;
  margin: auto;
}
.login-page .form .login{
  margin-top: -31px;
margin-bottom: 26px;
}
.login-header{
    padding:10px ;
}
.form {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

  
  .eye-icon {
    position: absolute;
    top: 50%; 
    right: 10px;
    transform: translateY(-50%); 
    cursor: pointer;
    z-index: 1; 
  }
  
.form input{
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background-color: #328f8a;
  background-image: linear-gradient(45deg,#328f8a,#08ac4b);
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.submitBtn:hover{
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
background-color: rgba(20, 153, 215, 0.3);
}
.form .message a {
  color: rgba(18, 88, 210, 0.3);
  font-size: 15px;
  text-decoration: none;
}


.main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 153, 215, 0.3);
  z-index: -1;
}